// src/pages/SignUp.jsx

import { Helmet } from "react-helmet";
import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import "./Signup.css";
import { createUserWithEmailAndPassword, signInWithPopup } from "firebase/auth";
import { auth, googleProvider, facebookProvider } from '../../Firebaseconfig';
import { FaGoogle, FaFacebookF, FaUser, FaEnvelope, FaLock } from "react-icons/fa";

const SignUp = () => {
  const navigate = useNavigate();
  const [form, setForm] = useState({ name: "", email: "", password: "" });

  const handleChange = (e) => setForm({ ...form, [e.target.name]: e.target.value });

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      await createUserWithEmailAndPassword(auth, form.email, form.password);
      alert("Sign Up Successful!");
      navigate("/login");
    } catch (err) {
      alert(err.message);
    }
  };

  const handleGoogleSignUp = async () => {
    try {
      await signInWithPopup(auth, googleProvider);
      alert("Google Sign Up Successful!");
      navigate("/login");
    } catch (err) {
      console.log(err.message);
    }
  };

  const handleFacebookSignUp = async () => {
    try {
      await signInWithPopup(auth, facebookProvider);
      alert("Facebook Sign Up Successful!");
      navigate("/login");
    } catch (err) {
      console.log(err.message);
    }
  };

  return (
    <><Helmet>
  <title>Sign Up | Your App Name</title>
  <meta name="description" content="Create an account to unlock all features." />
</Helmet>
    
    <div className="login-container">
      <div className="login-box">
        <h2>Sign Up</h2>
        <div className="login-subtext">Create your account to get started</div>

        <form onSubmit={handleSubmit}>
          <div className="input-group">
            <span className="input-icon"><FaUser /></span>
            <input 
              type="text" 
              name="name" 
              placeholder="Name" 
              value={form.name} 
              onChange={handleChange} 
              required 
            />
          </div>

          <div className="input-group">
            <span className="input-icon"><FaEnvelope /></span>
            <input 
              type="email" 
              name="email" 
              placeholder="Email" 
              value={form.email} 
              onChange={handleChange} 
              required 
            />
          </div>

          <div className="input-group">
            <span className="input-icon"><FaLock /></span>
            <input 
              type="password" 
              name="password" 
              placeholder="Password" 
              value={form.password} 
              onChange={handleChange} 
              required 
            />
          </div>

          <button type="submit" className="login-btn">Sign Up</button>
        </form>

        <div className="signup-text">Or sign up using</div>
        <div className="social-login">
          <FaGoogle onClick={handleGoogleSignUp} />
          <FaFacebookF onClick={handleFacebookSignUp} />
        </div>
      </div>
    </div>
    </>
  );
};

export default SignUp;
